import React, { useEffect, useState } from 'react';

import Project from './Project';
import Selfport from "../svg.js";
import Blurb from './Blurb';
import '../App.css';

const HomeScreen = () => {
  const [show, setShow] = useState(true)
  const [fixed, setFixed] = useState(false)
  const ShowBlu = () => {
    if (window.scrollY > 500) {
      setShow(false)
    }
    else {
      setShow(true)
    }
  }
  const fixedTitle = () => {
    if (window.scrollY > 500) {
      setFixed(true)
    }
    else {
      setFixed(false)
    }
  }

  useEffect(() => {
    console.log("mount"); //need fix
    window.addEventListener('scroll',ShowBlu, fixedTitle) 
    return () => {
      window.removeEventListener('scroll', ShowBlu, fixedTitle
    )
    }
  }, [])

  
  return(
    <div className='App'>
      {show ? <div className="intro">
        <div class="logo">
          <Selfport/>
        </div>
        <div class="title">
          <p>Hello!</p>
          <p style={{fontStyle:"normal"}}>안녕하세요~</p>
          <p>Goedendag,</p>
        </div>
      </div> : null}


      <div class="wave">
        <Blurb/>
        <svg height="1974" viewBox="0 0 1968 1974" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path d="M0 1974V403.87C18.4105 378.27 241.893 522.318 387.964 524.989C570.552 528.328 525.758 263.184 744.085 259.845C962.411 256.505 1022.25 348.639 1223.68 348.639C1425.11 348.639 1533.69 14.3587 1770.86 1.00219C2008.03 -12.3543 1964.94 259.845 1964.94 259.845V1974H0Z" fill="#F7EDE3"/>
        </svg>
      </div>
      
      {/* <div class="waves-container">
        <svg viewBox="0 0 1968 1000" height="1000">
          <path fill="#f7ede3" fill-opacity='1'/>
          <path fill="#F6BF62" fill-opacity='1'/>
          <path stroke="#F5CAC4" strokeWidth='4pt' fill="transparent"/>
          <path fill="#F28583" fill-opacity='1'/>  
        </svg>
      </div>  */}

      <div class="proj" >
        <Project/>
      </div>  


    </div>  
  )
}
export default HomeScreen;