import React, { useEffect, useState } from 'react';
import logo from './assets/small icon.png';
import { Link } from 'react-router-dom';
import './App.css';

const Topbar = () => {
  const [show, setShow] = useState(false)
  const controlBar = () => {
    if (window.scrollY > 200) {
      setShow(true)
    }
    else {
      setShow(false)
    }
  }

  useEffect(() => {
    window.addEventListener('scroll',
    controlBar) 
    return () => {
      window.removeEventListener('scroll',
      controlBar)
    }
  }, [])

  return(
    <div className={`topbar ${show && 'topbar__color'}`} style={{zIndex: "10"}}>
      {show ? <div className='topbar__layout'>
        <Link to="/portfolio">
            <div className="title" style={{marginLeft: "10%", maxHeight: "35px"}}>
              <p style={{fontSize: "25px", margin: ".2em 0", color: "#60065D"}}>jwfung</p> 
            </div>
          </Link>
        <div className="three-col"> 
          <a href="/portfolio/#projects" spy={true} smooth={true}> projects </a> 
          <Link to="/portfolio/about" spy={true} smooth={true}> about </Link> 
        </div> 
      </div> : null }
    </div>  
    )
}

export default Topbar;